  padding: 0;
  height: 100%;
  width: 100%;
  overflow-x: hidden; }

body {
  font-family: 'Roboto', sans-serif; }

.btn {
  border-radius: 0;
  width: 100%; }
  .btn:hover, .btn:focus, .btn:active {
    color: #fff; }

.btn-blue {
  background-color: #00a8a9;
  color: #fff; }

.btn-yellow {
  background-color: #f0cc50;
  color: #fff; }

::-webkit-input-placeholder {
  /* WebKit, Blink, Edge */
  color: #333; }

input:focus::-webkit-input-placeholder {
  color: #fff; }

:-moz-placeholder {
