  opacity: 1; }

input:focus::-moz-placeholder {
  color: #fff; }

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #333; }

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #333; }

input:focus:-ms-input-placeholder {
  color: #fff; }

.navbar {
  background-color: #37393e;
  color: #fff;
  padding: 0; }
  @media all and (max-width: 575px) {
    .navbar {
      padding: 10px 0; } }
  .navbar .row {
    width: 100%; }
  .navbar .btn {
    font-size: 30px;
    font-weight: 300;
    width: 50%;
    height: 60px; }
    @media all and (max-width: 991px) {
      .navbar .btn {
        font-size: 25px;
        line-height: 45px; } }
    @media all and (max-width: 575px) {
      .navbar .btn {
        width: 100%;
        line-height: 35px;
        height: 50px; } }
    .navbar .btn .btn-img {
      margin: -10px 10px 0 0; }

@media all and (min-width: 992px) {
  .navbar-nav {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around; } }

.navbar-nav .nav-link {
  color: #fff;
  font-size: 16px;
  padding: 18px 0; }

.navbar-toggler {
  border: 1px solid #fff;
  border-radius: 0;
  margin: 10px 0 0 0; }
  @media all and (min-width: 992px) {
    .navbar-toggler {
      display: none; } }
  @media all and (max-width: 575px) {
    .navbar-toggler {
      margin: 0 0 10px 0; } }
  .navbar-toggler .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E"); }

@media all and (min-width: 992px) {
  .collapse {
    display: block; } }

.header {
  overflow: hidden;
  padding: 10px 0; }
  .header .form-wrapper {
    padding: 20px 0 0 0; }
  .header .form-inline {
    width: 70%;
    position: relative; }
    .header .form-inline .form-control {
      border-radius: 0;
      width: 100%; }
  .header .search-btn {
    background-color: #fff;
    border: none;
    opacity: 0.7;
    position: absolute;
    right: 10px;
    top: 7px; }
  .header .btn {
    width: 25%; }

.navbar-brand {
  max-width: 230px;
  margin: 0; }
  @media all and (max-width: 575px) {
    .navbar-brand {
      margin: auto;
      display: block; } }
  .navbar-brand .logo {
    float: left;
    width: 90%; }
    @media all and (max-width: 575px) {
      .navbar-brand .logo {
        margin: auto;
        display: block; } }

.cart-items {
  margin: 0 0 0 10px; }

.slider {
  width: 100%;
  background: #ddd;
  position: relative; }
  .slider .slide {
    width: 100%;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat; }
    .slider .slide:after {
      content: "";
      display: block;
      padding-bottom: 40%; }
  .slider .next-arrow,
  .slider .back-arrow {
    position: absolute;
    top: 45%;
    cursor: pointer;
    color: #fff;
    -webkit-transition: all ease-in 0.2s;
    -o-transition: all ease-in 0.2s;
    transition: all ease-in 0.2s;
    max-width: 50px; }
    .slider .next-arrow:hover,
    .slider .back-arrow:hover {
      opacity: 0.7;
      -webkit-transition: all ease-in 0.2s;
      -o-transition: all ease-in 0.2s;
      transition: all ease-in 0.2s; }
  .slider .back-arrow {
